.sword-menu {
  font-size: 0.15rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.sword {
  transition: all 500ms ease-in-out;

  &:nth-child(2) {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
    transform-origin: left;
    transition: all 350ms ease-in-out;
  }
}

.sword--open {
  &:nth-child(1) {
    transform: rotate(-405deg) translate(-2em, 3em);
  }

  &:nth-child(2) {
    transform: translate(500%) rotate(180deg);
    opacity: 0;
  }

  &:nth-child(3) {
    transform: rotate(405deg) translate(-2em, -3em);
  }
}

.l-container {
  width: 3em;
  margin: -2rem auto;
}

.l-sword {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}

.l-handle-orb {
  width: 0.2em;
  display: block;
  margin: 0 auto;
  position: relative;
  top: 0.3em;
  z-index: -1;
}

.l-handle-orb:after {
  content: ' ';
  display: block;
  position: relative;
  /* width: 0.3em;
    height: 0.3em; */
}

.handle-orb {
  color: #fff200;
}

.handle-orb:after {
  border-radius: 1.25em;
  border: 0.3em solid #fff200;
  background-color: #fff200;
}

.l-handle-block {
  width: 64%;
  height: 1.25em;
  display: block;
  margin: 0 auto;
  background-color: #fff200;
}

.l-small-handle-block {
  width: 75%;
  height: 0.65em;
  display: block;
  margin: 0 auto;
  background-color: #fff200;
}

.handle-block {
  color: #fff200;
}

