.handle-block-curve {
  background-color: #fff200;
}

.l-handle {
  width: 60%;
  height: 4.4em;
  display: block;
  margin: 0 auto;
}

.l-handle:after {
  content: ' ';
  display: block;
  position: relative;
  margin: 0 auto;
}

.handle {
  /* background-image: repeating-linear-gradient(180deg, #fff200 0.125em, #fff200 0.5em); */
  background-color: #fff200;
}

.l-blade {
  height: 12.5em;
  position: relative;
}

.l-blade-top {
  width: 0;
  height: 0;
  position: absolute;
  left: 10%;
}

.blade-top {
  border-left: 1.25em solid transparent;
  border-right: 1.25em solid transparent;
  border-top: 1.25em solid #fff200;
}

.l-blade-left {
  position: absolute;
  top: 0;
  left: 14%;
  width: 42%;
  height: 125%;
  z-index: -1;
}

.l-blade-right {
  position: absolute;
  top: 0;
  right: 14%;
  width: 42%;
  height: 125%;
  z-index: -1;
}

.blade-left {
  background-color: #fff200;
}

.blade-right {
  background-color: #fff200;
}

.l-blade-bottom {
  left: 0.5em;
  top: 13.4em;
  width: 0;
  height: 0;
  border: 1.1em solid transparent;
  border-bottom-color: #fff200;
  position: relative;
}

.l-blade-bottom:after {
  content: '';
  position: absolute;
  left: -1.25em;
  top: 1.1em;
  width: 0.2em;
  height: 0;
  border: 1.1em solid transparent;
  border-top-color: #fff200;
}

